import React from 'react';
import { ComposableMap, Geographies, Geography, Annotation } from 'react-simple-maps';
import worldMapData from './WorldMap Data/ne_10m_admin_0_countries.json';
import './WorldMap.css';

const countryHighlight = {
    USA: "rgba(49,118,84,1)", // green
    CHN: "rgba(49,118,84,1)", // green
    GBR: "rgba(49,118,84,1)", // green
    CAN: "rgba(49,118,84,1)", // green
  };
  

function WorldMap() {
  return (
    
    <div className='map-container'>
        <div className="mapWrapper">
        <ComposableMap 
            width={400} 
            height={150}
            viewBox="0 -15 400 150" 
            projection="geoEqualEarth"
            projectionConfig={{
                scale: 60,
                
            }}
            pointerEvents="none">
            <Geographies geography={worldMapData}>
            {({ geographies }) =>
              geographies.map(geo => {
                  console.log(geo); // Add this line
                  return (
                    <Geography
                      key={geo.rsmKey}
                      geography={geo}
                      fill={countryHighlight[geo.properties.ISO_A3] || "#EEE"} // apply color or default
                    />
                  );
                })
            }
            </Geographies>
            <Annotation
                subject={[-98, 38]}
                dx={-35}
                dy={10}
                style={{
                    stroke: "#000", // line color
                    strokeWidth: 0.5, // line thickness
                  }}
            >
            <text x={-7} y={-4} textAnchor="middle" alignmentBaseline="middle" fontSize={4} stroke="#000" strokeWidth={0.2}>
                USA: 30%
            </text>
            </Annotation>
            <Annotation
                subject={[-100.7, 55]} // Coordinates for Canada
                dx={-40}
                dy={-20}
                style={{
                    stroke: "#000", // line color
                    strokeWidth: 0.5, // line thickness
                  }}
            >
            <text x={-7} y={-4} textAnchor="middle" alignmentBaseline="middle" fontSize={4} stroke="#000" strokeWidth={0.2}>
                CAN: 15%
            </text>
            </Annotation>
            <Annotation
                subject={[0, 52]} // Coordinates for Canada
                dx={-15}
                dy={10}
                style={{
                    stroke: "#000", // line color
                    strokeWidth: 0.5, // line thickness
                  }}
            >
            <text x={-7} y={-4} textAnchor="middle" alignmentBaseline="middle" fontSize={4} stroke="#000" strokeWidth={0.2}>
                GBR: 15%
            </text>
            </Annotation>
            <Annotation
                subject={[105, 35]} // Coordinates for Canada
                dx={40}
                dy={17}
                style={{
                    stroke: "#000", // line color
                    strokeWidth: 0.5, // line thickness
                  }}
            >
            <text x={10} y={-4} textAnchor="middle" alignmentBaseline="middle" fontSize={4} stroke="#000" strokeWidth={0.2}>
                CHN: 15%
            </text>
            </Annotation>
        </ComposableMap>
        </div>
    </div>
  );
}

export default WorldMap;
